<template>
  <el-container class="password">
    <el-header>
      <el-button>CREATE</el-button>
      <el-button>SEARCH</el-button>&#8195;
      <el-input v-model="search" placeholder="Enter Keyword" prefix-icon="el-icon-search"></el-input>&#8195;&#8195;CODE:&#160;&#160;
      <el-input v-model="code" placeholder="Enter Code" show-password></el-input>
    </el-header>
    <el-main>
      <el-table :data="tableData" style="width: 100%" border max-height="500">
        <el-table-column align="center" fixed prop="date" label="DESCRIPTION" width="150"></el-table-column>
        <el-table-column align="center" prop="name" label="ACCOUNT" width="120"></el-table-column>
        <el-table-column align="center" prop="province" label="PASSWORD" width="120"></el-table-column>
        <el-table-column align="center" prop="city" label="COMMENTS" width="120"></el-table-column>
        <el-table-column align="center" prop="address" label="COMMENTS" width="300"></el-table-column>
        <el-table-column align="center" prop="zip" label="COMMENTS" width="120"></el-table-column>
        <el-table-column align="center" fixed="right" label="OPERATIONS" width="150">
          <template slot-scope="scope">
            <el-button type="text" size="small">S/H</el-button>
            <el-button @click="handleClick(scope.row)" type="text" size="small">EDIT</el-button>
            <el-button type="text" size="small">DEL</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'Password',
  data() {
    return {
      msg: 'PASSWORD',
      code: '',
      search: '',
      tableData: [{
        id: 1,
        date: 'SHANE',
        name: 'SHANE',
        province: 'SHANE',
        city: 'SHANE',
        address: 'SHANE',
        zip: 'SHANE'
      }]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-input {
  width: 150px;
}
.password {
  margin-top: 20px;
}
</style>



