<template>
  <el-container class="memo">
    <el-aside width="20%">
      <el-tree
        :data="category"
        node-key="id"
        @node-click="handleNodeClick"
        :props="defaultProps"
        highlight-current
      ></el-tree>
    </el-aside>
    <el-main>
      <el-button disabled type="info">{{info}}</el-button>
      <el-button>UPDATE</el-button>
      <el-input type="textarea" v-model="content" autosize style="margin-top:10px"></el-input>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'Memo',
  data() {
    return {
      msg: 'Memo',
      category: [{
        label: 'TOPIC'
      }],
      content: '',
      active: 0,
      info: 'EMPTY',
      defaultProps: {
        label: 'label'
      }
    }
  },
  methods: {
    handleNodeClick(data) {
      this.info = data.label
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-aside {
  margin-top: 20px;
}
</style>



